  line-height: 1.45;
  background-color: #f6f8fa;
  border-radius: 0 3px 3px 0;
  border: none;
  border-left: 2px solid #1abc9c;
}
.nc-post-content em {
  font-weight: inherit;
  font-style: italic;
}
.nc-post-content > :last-child {
  margin-bottom: 0 !important;
}
.nc-post-content pre {
  word-wrap: normal;
}
.nc-post-content pre > code {
  padding: 0;
  margin: 0;
  font-size: 100%;
  word-break: normal;
  white-space: pre;
  background: 0 0;
  border: 0;
}
.nc-post-content pre code,
.nc-post-content pre tt {
  display: inline;
  max-width: auto;
  padding: 0;
  margin: 0;
  overflow: visible;
  line-height: inherit;
  word-wrap: normal;
  background-color: transparent;
  border: 0;
}
.nc-post-content code,
.nc-post-content tt {
  padding: 0.2em 0.4em;
  margin: 0;
  font-size: 85%;
  background-color: rgba(27, 31, 35, 0.05);
  border-radius: 3px;
}
.nc-post-content table {
  display: block;
  overflow: auto;
  width: auto;
  border: none;
  max-width: none;
  border-collapse: collapse;
}
.nc-post-content table tr {
  background-color: #fff;
  border-top: 1px solid #c6cbd1;
}
.nc-post-content table td,
.nc-post-content table th {
  padding: 6px 13px;
  border: 1px solid #dfe2e5;
  min-width: 50px;
}
.nc-post-content table th {
  font-weight: 600;
}
.nc-post-content table tr:nth-child(2n) {
  background-color: #f6f8fa;
}
ul.contains-task-list li.task-list-item {
  list-style-type: none;
}
.task-list-item-checkbox {
  margin: 0 0.2em 0.25em -1.6em;
  vertical-align: middle;
}
.fixed-foot {
  background: #2a2f35;
  position: fixed;
  bottom: 0;
  width: 100%;
  z-index: 20;
}
.fixed-foot-main {
  width: 1200px;
  height: 30px;
  margin: 0 auto;
  line-height: 30px;
  color: #fff;
  font-size: 18px;
  position: relative;
}
.fixed-foot-tip {
  font-size: 16px;
  color: #fff;
  padding: 0 20px;
  float: left;
}
.fixed-foot-login {
  float: right;
  font-size: 12px;
  color: #999;
}
.fixed-foot-login .more-login {
  color: #999;
}
.ft-login-item {
  background: url(https://static.nowcoder.com/images/img/icons/ico-login2.png)
    no-repeat;
  display: inline-block;
  vertical-align: middle;
  width: 20px;
  height: 20px;
  margin-right: 10px;
}
.ft-login-wb {
  background-position: 0 0;
}
.ft-login-qz {
  background-position: -30px 0;
  width: 16px;
}
.ft-login-wx {
  background-position: -56px 0;
}
.ft-login-git {
  background-position: -85px 0;
}
.collect-fixed-foot {
  background: #2a2f35;
  position: fixed;
  bottom: 0;
  width: 100%;
  z-index: 20;
}
.collect-fixed-foot-main {
  width: 1200px;
  height: 80px;
  line-height: 80px;
  margin: 0 auto;
  color: #fff;
  font-size: 20px;
  position: relative;
  cursor: pointer;
}
.collect-fixed-foot-main .icon-nc-close {
  color: #6d7a89;
  position: absolute;
  top: 10px;
  right: 30px;
